.trending {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media (max-width: 650px) {
  .trending {
    width: 100%;
    justify-content: center;
  }
}

.pageTitle {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: sans-serif;
  font-size: 2vw;
  padding: 4px;
  border-radius: 50px;
  color: #fff;
}
