.App {
  min-height: 100vh;
  background-color: #0d111c;
  /* color: #fff; */
}

/* form data start */

.container-2 {
  display: flex !important;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* height: 70vh;
  max-width: 500px;
  color: #0d111c;
  width: 100%; */
  margin: 0px auto;
  /* border: 5px solid yellow; */
  border-radius: 10px;
}
.container-2 > form {
  width: 100%;
  height: 70vh;
  border-radius: 5px;
  background: #fff;
  text-align: center;
}

.login-container {
  padding: 10px 20px;
  background: #fff;
  height: 70vh;
  max-width: 500px;
  color: #0d111c;
  width: 100%;
  align-items: center;
  margin-top: 80px;
  border-radius: 10px;
}

.login-container > h1 {
  color: #0d111c;
}

.login-container > p {
  color: #0d111c;
  text-align: center;
}

::placeholder {
  color: #0d111c;
}

.field > input {
  border-radius: 10px;
  padding: 10px 70px;
  width: 100%;
}

.fluid_ui {
  width: 100%;
  background-color: #ff7d65;
}

/* form data ends */

/* header data start */

.container {
  width: 100%;
  /* z-index: -1; */
  position: relative;
  text-align: center;
  color: white;
  line-height: 1.6;
  overflow: hidden;
}

.register-form {
  /* padding-top: 100px; */
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.register-form > h1 {
  color: #fff;
  margin: auto;
}

.register-form > h2 {
  color: black;
}

.register-form > p {
  color: #fff;
  /* width: 80%; */
  width: 100%;
  margin: auto;
}

.homepage {
  /* width: 100vw; */
}

.home-button {
  padding: 10px 20px;
  border-radius: 15px;
  color: #fff;
  background-color: #ff1744;
  border: none;
}

h1 {
  text-align: center;
}

pre {
  width: 70%;
  color: #fff;
  font-size: larger;
}

button {
  padding: 10px 100px;
  color: #fff;
  border-radius: 10px;
  transition-duration: 0.4s;
  background: #0563b4;
}

button:hover {
  color: white;
}

p {
  color: red;
}

/* header data  end */

@media (min-width: 700px) {
  .container {
    width: 100%;
    /* z-index: -1; */
    position: relative;
    text-align: center;
    color: white;
    line-height: 1.6;
    overflow: hidden;
  }

  .register-form {
    /* padding-top: 100px; */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .register-form > h1 {
    color: #fff;
    width: 104%;
    margin: auto;
  }

  .register-form > h2 {
    color: black;
  }

  .register-form > p {
    color: #fff;
    width: 70%;
    margin: auto;
  }

  .homepage {
    width: 100vw;
  }

  .home-button {
    padding: 10px 20px;
    border-radius: 15px;
    color: #fff;
    background-color: #ff1744;
    border: none;
  }

  h1 {
    text-align: center;
  }

  pre {
    width: 70%;
    color: #fff;
    font-size: larger;
  }

  button {
    padding: 10px 100px;
    color: #fff;
    border-radius: 10px;
    transition-duration: 0.4s;
    background: #0563b4;
  }

  button:hover {
    color: white;
  }

  p {
    color: red;
  }
}
