.ContentModal-parent {
  background-color: rgb(2, 0, 36);
  width: 100%;
  height: 100vh;
  position: relative;
}

.ContentModal {
  display: flex;
  flex-direction: row;
  width: 90%;
  height: 100%;
  overflow: hidden;
  /* justify-content: end;
  height: 100%;
  width: 100%;
  overflow-y: scroll; */
  scrollbar-width: none;
}

.ContentModal__landscape {
  color: #fff;
  margin-top: 50px;
  object-fit: contain;
  border-radius: 10px;
  /* width: 60%; */
  display: none;
  height: 60%;
}

/* .content-right {
  height: 50%;
} */

.tagline {
  padding-bottom: 10px;
  align-self: center;
  color: #fff;
}

.ContentModal__description {
  color: #fff;
  display: flex;
  height: auto;
  padding: 35px;
  width: 120%;
  margin: auto;
  /* overflow: hidden; */
  /* border-radius: 20px; */
  scrollbar-width: thin;
  /* box-shadow: inset 0 0 5px #000000; */
  /* text-align: justify; */
}

.ContentModal__title {
  color: #fff;
  /* height: 12%; */
  font-size: 7vw;
  padding-top: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ContentModal__about {
  padding: 10px;
  width: 95%;
  height: 90%;
  /* width: 800px; */
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  justify-content: space-evenly;
  font-weight: 300;
}

.ContentModal__portrait {
  /* display: none; */
  /* width: 50%; */
  margin: 0px auto;
  top: 70px;
  right: 40px;
  height: 50%;
  width: 30%;
  object-fit: contain;
  border-radius: 10px;
  position: absolute;
}

