.ContentModal__description::-webkit-scrollbar {
  display: none;
}

.span-rating {
  color: #fff;
}

@media (min-width: 600px) {
  .tagline {
    padding-bottom: 10px;
    align-self: center;
    color: #fff;
    position: relative;
  }

  .ContentModal__portrait {
    /* display: none; */
    /* width: 50%; */
    margin: 0 auto;
    height: 70%;
    object-fit: contain;
    border-radius: 10px;
  }

  .ContentModal__landscape {
    color: #fff;
    margin-top: 50px;
    object-fit: contain;
    border-radius: 10px;
    /* width: 60%; */
    display: none;
    height: 60%;
  }

  .ContentModal__description {
    color: #fff;
    display: flex;
    height: auto;
    /* padding: 35px; */
    width: 70%;
    padding: 0px;
    margin: 0px;
    /* background-color: red; */
    /* overflow: hidden; */
    /* border-radius: 20px; */
    scrollbar-width: thin;
    /* box-shadow: inset 0 0 5px #000000; */
    /* text-align: justify; */
  }

  .ContentModal__title {
    color: #fff;
    height: 12%;
    font-size: 3vw;
    padding-top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .ContentModal__about {
    padding: 10px;
    width: 95%;
    height: 90%;
    /* width: 800px; */

    display: flex;
    flex-direction: column;
    font-family: "Roboto", sans-serif;
    justify-content: space-evenly;
    font-weight: 300;
  }
}

@media (min-width: 835px) {
  .ContentModal__portrait {
    display: none;
  }
  .ContentModal__landscape {
    display: flex;
    width: 100%;
  }
  .ContentModal {
    flex-direction: row;
    justify-content: space-around;
    padding: 10px 0;
  }
  .ContentModal__about {
    width: 58%;
    padding: 0;
    height: 100%;
  }
  .ContentModal__title {
    font-size: 3.5vw;
  }
  .ContentModal__description {
    /* font-size: 22px; */
    margin-left: 50px;
  }
  .span-rating {
    width: 90%;
    margin: 0px auto;
  }
}
