.media {
  display: flex;
  flex-direction: column;
  width: 290px;
  /* padding: 5px; */
  margin: 10px 20px;
  /* background-color: #282c34; */
  position: relative;
  background-color: #24446b;
  color: #fff;
  height: auto;
  font-family: "Montserrat", sans-serif;
}

.media:hover {
  color: black;
}

@media (max-width: 500px) {
  .media {
    width: 46%;
  }
}

.poster {
  border-radius: 10px;
  object-fit: cover;
  /* width: 210px; */
}

.title {
  width: 50%;
  margin-left: 50px;
  text-align: center;
  font-size: 17px;
  padding: 8px 0;
}

.subTitle {
  display: flex;
  justify-content: space-between;
  padding-bottom: 3px;
  padding: 0 2px;
  /* border: 5px solid red; */
}

.pointer {
  cursor: pointer;
}

